import React, { useState, useEffect } from 'react';

import useProducts from '../../../../hooks/ProductHandlers';
import useCompounds, { useCompoundItem } from '../../../../hooks/CompoundHandlers';

import EditSaveButton from '../../../shared/EditSaveButton/EditSaveButton';
import ProductCard from '../../../ProductCard/ProductCard';
import ViewProduct from '../../Products/ViewProduct/ViewProduct'
import ModalBase from '../../../shared/ModalBase/ModalBase';
import AddProduct from '../../Products/AddProduct/AddProduct'
import ListDemands from '../ListDemands/ListDemands';

import { Dropdown } from 'primereact/dropdown';
import { InputText } from 'primereact/inputtext';
import { confirmDialog } from 'primereact/confirmdialog';
import { ButtonBase } from '@mui/material';
import { Checkbox } from 'primereact/checkbox';

import ViewLogCompound from "../ViewLogCompound/ViewLogCompound";

import './ViewProductCompound.css'

export default function ViewProductCompound(props) {
    const [editing, setEditing] = useState(false)
    const { products } = useProducts()
    const { compounds, updateCompounds } = useCompounds()
    const { 
        item, deleteItem, patchItem, 
        patchImage
    } = useCompoundItem(props.data.id)
    const [details, setDetails] = useState({
        product: props.data.product,
        observations: props.data.observations,
        period: props.period,
        typed: props.data.typed,
        image: props.data.image?.split('/').at(-1)
    })
    const [selected, setSelected] = useState({})

    useEffect(() => {
        setDetails({
            ...item, 
            period: props.period,
            product: props.data.product,
            typed: props.data.typed
        })
    }, [])
    
    const handleEdit = () => {
        setEditing(true)
    }

    useEffect(() => {
        props.backdropAutoClose(!editing)
    }, [editing])

    useEffect(() => {
        const selected = JSON.parse(localStorage.getItem("@greenmix:SELECTED_COMPOUND"))
        if (!selected) {
            setSelected(compounds[0])
        } else {
            setSelected(compounds?.find(compound => compound.id === selected.id))
        }
    }, [])
    

    const handleSave = () => {
        if (details.product && details.period) {
            patchItem(props.data.id, details)
            if (details.product.generic && details.image) {
                patchImage(props.data.id, details.image)
            }
            setEditing(false)
        } else {
            alert("Por favor verifique os dados inseridos")
        }
    } 


    const confirmRemove = () => {
        confirmDialog({
            message: `Remover item?`,
            header: 'Remover item composto',
            icon: 'pi pi-exclamation-triangle',
            acceptLabel: 'Sim',
            rejectLabel: 'No',
            accept: () => {
                deleteItem(props.data.id, props.closeModal)
            },
            reject: () => {}
        });
    }


    const handleCancel = () => {
        setDetails({
            product: props.data.product,
            observations: props.data.observations,
            period: props.period,
        })
        setEditing(false) 
    }

    return (
        <div className='viewProductCompound'>
            <div className="p-grid">
                <div className="p-col">
                    <div className='productContainer'>
                        <ModalBase
                            caller={<ProductCard 
                                data={details.product} 
                                doubleSize 
                                genericImg={
                                    details.product.generic ? 
                                    props.genericImg : undefined
                                }
                                key={details.product.reference}
                            />}
                            content={<ViewProduct productId={details.product.id} />}
                            title={details.product.reference}
                        />
                    </div>
                </div>
                <div className="p-col">
                    <div className='detailsContainer p-grid p-dir-col'>
                        <div className="pcol">
                            <div className="p-grid p-dir-col">
                                <div className="p-col details--product">
                                    <h4>Produto</h4>
                                    <Dropdown value={details.product} 
                                        onChange={(e) => setDetails({...details, product: e.target.value})}
                                        disabled={!editing}
                                        filter
                                        options={products}
                                        optionLabel='reference'
                                        placeholder='Selecione um produto'
                                    />
                                    {editing ? 
                                    <>
                                        <p>ou</p>
                                        <ModalBase
                                            caller={<div className='details--createProduct'>Criar novo produto</div>}
                                            content={<AddProduct />}
                                            title="Adicionar PRODUTO ao COMPOSTO"
                                        />
                                    </> : <></>
                                    }
                                </div>
                                <div className="p-col">
                                    <h4>Perodo</h4>
                                    <Dropdown value={details.period}
                                        onChange={(e) => setDetails({...details, period: e.target.value})}
                                        disabled={!editing}
                                        options={selected?.periods}
                                        optionLabel='name'
                                        placeholder='Selecione um perodo'
                                    />
                                </div>
                                <div className="p-col">
                                    <h4>Observaes</h4>
                                    <InputText value={details.observations} 
                                        onChange={(e) => setDetails({...details, observations: e.target.value})}
                                        disabled={!editing}
                                    />
                                </div>
                                <div className="p-col" style={{display: "inline-flex", gap: "0.25rem", marginBottom: "0.5rem"}}>
                                    <Checkbox onChange={
                                        e => setDetails({...details, typed: e.checked})
                                    } 
                                    checked={details.typed} 
                                    disabled={!editing}/>
                                    <p>Pedido digitado</p>
                                </div>
                                { editing && details.product.generic ?
                                <div className="p-col">
                                    <label className="salmonBorderButton" htmlFor='selectButton'>
                                        Imagem genrica: {
                                            details.image ?
                                                typeof details.image === "string"
                                                ? details.image : details.image.name
                                            : "sem imagem"
                                        }
                                        <input id='selectButton'
                                            type="file" accept='image/*'
                                            onChange={(e) => {
                                                setDetails(d => ({...d, image: e.target.files[0]}))
                                            }}
                                        />
                                    </label>
                                </div> : null
                                }
                                { !editing ?
                                <div className="p-col" style={{marginBottom: "0.5rem"}}>
                                    <ModalBase 
                                        caller={
                                        <div className='greenBorderButton'>
                                            Demandas
                                        </div>
                                        }
                                        content={<ListDemands 
                                                item={props.data} 
                                                onClose={() => {
                                                    updateCompounds()
                                                }}
                                                period={props.period}
                                            />
                                        }
                                        title="Visualizar demandas"
                                    />
                                </div> : null
                                }
                                <div className="p-col">
                                    <ModalBase
                                        title="Histrico do PRODUTO no COMPOSTO"
                                        caller={
                                        <div className='yellowBorderButton'>
                                            <p>Histrico do produto no composto</p>
                                        </div>
                                        }
                                        content={<ViewLogCompound compoundId={props.data?.id}/>}
                                    />
                                </div>
                            </div>
                        </div>
                        <div className="pcol buttons">
                            <EditSaveButton onEdit={handleEdit} onSave={handleSave} onCancel={handleCancel}/>
                            { !editing ?
                            <ButtonBase className='redButton' onClick={confirmRemove}>
                                Remover item
                            </ButtonBase> : null
                            }
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}