import React, { useEffect } from 'react'
import './style.css'
import 'primeflex/primeflex.css';

import { DataView } from 'primereact/dataview';

import { TextField } from '@mui/material';

import SideBar from '../../components/SideBar/SideBar';
import ProductCard from '../../components/ProductCard/ProductCard';
import ModalBase from '../../components/shared/ModalBase/ModalBase';
import ViewProduct from '../../components/Modals/Products/ViewProduct/ViewProduct';
import AddProduct from '../../components/Modals/Products/AddProduct/AddProduct';

import { isAuthenticated } from '../../services/api';
import useProducts from '../../hooks/ProductHandlers';

const ProductsPage = () => {
    useEffect(() => {
        isAuthenticated()
    }, [])

    const { 
        products, 
        isLoading, 
        productFilters,
        filterProducts,
        updateProducts
    } = useProducts()

    const itemTemplate = (data, layout) => {
        if (layout === 'grid') {
            return (
                <div className="p-col">
                    <ModalBase
                        caller={<ProductCard data={data} />}
                        content={<ViewProduct productId={data.id} />}
                        title={data.reference}
                        onClose={updateProducts}
                    />
                </div>
            );
        }
    }

    const Products = () => {
        return (
            <DataView
                className='p-justify-start'
                value={products} 
                layout="grid" 
                itemTemplate={itemTemplate} 
                paginator rows={12}
                totalRecords={products?.length}
                emptyMessage="Nenhum produto registrado"
            />
        )
    }

    const PageContent = () => {
        return (
            <>
                <div className='main'>
                    <Products/>
                </div>
                <div className='footer'>
                    <ModalBase
                        caller={
                            <div className='FAB addButton'>
                                <h1>+</h1>
                            </div>
                        }
                        content={<AddProduct />}
                        title="Adicionar Produto"
                    />
                </div>
            </>
        )
    }

    return (
        <div className='productPage page'>
            <SideBar 
                productFilters={productFilters}
                filterProducts={filterProducts} 
            />
            <div className='productPage--body'>
                <div className='header'>
                    <div className='header--content'>
                        <h2>Estes so os PRODUTOS</h2>
                        <div className='searchBar'>
                            <TextField id="outlined-basic" 
                            label="Pesquisar" 
                            variant="outlined" 
                            size="small"
                            color="success"
                            onChange={(e) => filterProducts("reference", e.target.value)}
                            />
                        </div>
                    </div>
                    <div className='header--border'></div>
                </div>
                {isLoading ? <h3 id="loadingText">Carregando...</h3> : <PageContent />}
            </div>
        </div>
    )
}

export default ProductsPage