import React, { useEffect, useState } from 'react';
import { usePeriodItems } from '../../hooks/CompoundHandlers';
import './PeriodView.css'

import ModalBase from '../shared/ModalBase/ModalBase';

import ProductCard from '../ProductCard/ProductCard';
import ViewProductCompound from '../Modals/Compounds/ViewProductCompound/ViewProductCompound';
import AddProductCompound from '../Modals/Compounds/AddProductCompound/AddProductCompound';
import CompoundPopover from '../CompoundPopover/CompoundPopover';


export default function PeriodsView(props) {
    const [sum, setSum] = useState(0)
    const period = props.period
    const { 
        items, isLoading, updateItems ,
        filters, filterPeriod
    } = usePeriodItems(period.id)

    useEffect(() => {
        if (filters) {
            props.periodHooks({
                filters: filters,
                setFilters: filterPeriod
            })
        }
    }, [filters])

    useEffect(() => {
        let sum = 0

        if (!isLoading) {
            if (filters.store !== "") {
                for (let i = 0; i < items.length; i++) {
                    for (let j = 0; j < items[i].demands.length; j++) {
                        if (items[i].demands[j].store.id === filters.store) {
                            sum += items[i].demands[j].quantity
                        }
                    }
                }
            } else {
                for (let i = 0; i < items.length; i++) {
                    for (let j = 0; j < items[i].demands.length; j++) {
                        sum += items[i].demands[j].quantity
                    }
                }
            }
        }

        setSum(sum)
    }, [items])

    return (
        <div className='periodView'>
            <div className='dates'>
                <span>{period.start_date}</span> a <span>{period.end_date}</span> / Demandas abertas: {sum}
            </div>
            <div className='viewContent'>
                {items?.map((item) => (
                    <CompoundPopover key={item.id} item={item}>
                        <ModalBase
                            caller={
                                    <ProductCard 
                                        data={item.product} typed={item.typed}
                                        genericImg={
                                            item.product.generic ? 
                                            item.image : undefined
                                        }
                                        key={
                                            item.product.generic && item.image 
                                            ? item.image : item.reference
                                        }
                                    />
                                }
                            content={<ViewProductCompound 
                                data={item} period={props.period} 
                                genericImg={item.image}
                            />}
                            onClose={updateItems}
                            title="Visualizar PRODUTO em COMPOSTO"
                        />
                    </CompoundPopover>
                ))}
                <ModalBase
                    caller={
                        <div className='FAB'>
                            <h1>+</h1>
                        </div>
                    }
                    content={<AddProductCompound period={props.period} compoundId={props.compoundId}/>}
                    title="Adicionar PRODUTO ao COMPOSTO"
                    onClose={updateItems}
                />
            </div>
        </div>
    )
}