import React, { useEffect, useState } from "react";
import { useDemands } from "../../../../hooks/DemandsHandlers";

import "./ViewLogDemand.css";

export default function ViewLogDemand(props) {
  const [logs, setLogs] = useState();
  const { logDemand } = useDemands("")

  useEffect(() => {
    logDemand(props.demandId).then((res) => setLogs(res));
  }, []);
  
  const ColoredLine = ({ color }) => (
    <hr
      style={{
        color: color,
        backgroundColor: color,
        height: 3,
        marginBottom: 20
      }}
    />
);

  return (
    <div className="viewLog">
      {logs?.map((log, index) => 
      <ul className="lista" key={index}>
        <li><ColoredLine color="rgb(166, 248, 0)"/>{
          log.history_type === "CREATE" ? 
            `
              A estimativa foi criada por meio do mtodo ${log.history_type} 
              pelo usurio ${log.history_user?.username} 
              em ${log.history_date.slice(8, 10) + "/" + log.history_date.slice(5, 7) + 
              "/" + log.history_date.slice(0, 4) + 
              " s " + log.history_date.slice(11, 16)}.
            ` 
              : 
            `
              A estimativa foi modificada por meio de ${log.history_type} 
              nos campos listados abaixo pelo usurio ${log.history_user.username} 
              em ${log.history_date.slice(8, 10) + "/" + log.history_date.slice(5, 7) + 
              "/" + log.history_date.slice(0, 4) + 
              " s " + log.history_date.slice(11, 16)}.
            `
            }</li>
        {log.history_type === "UPDATE" && (typeof log.changes === 'object' && log.changes !== null) ? 
        <ul>
          {Object.entries(log.changes).map((change, index) => 
            <li className="listaMargin" key={index}><span className="coloredTitle">{`${change[0]}`}</span>{`: valor ${change[1].old_value} foi alterado para ${change[1].new_value != "" ? change[1].new_value : "vazio"}.`}</li>)}
        </ul> : ""}
      </ul>
      )}
    </div>
  )
}
