import React, { useState, useEffect } from "react";
import { ButtonBase } from "@mui/material";
import { Sidebar } from "primereact/sidebar";
              
import "./CompoundPopover.css";

export default function CompoundPopover(props) {
  const [open, setOpen] = useState(false);
  const [total, setTotal] = useState(0);

  useEffect(() => {
    let sum = 0;

    for (let index = 0; index < props.item.demands.length; index++) {
      sum += props.item.demands[index].quantity;
    }

    setTotal(sum);
  }, [props.item]);

  return (
    <div className="popOver">
      <ButtonBase className="infoButton" onClick={() => setOpen(true)}>
        i
      </ButtonBase>
      <div>{props.children}</div>
      <Sidebar
        visible={open}
        onHide={() => setOpen(false)}
        className="sidebar--container"
      >
        <div className="sidebar--reference">
          <h2>{props.item.product.reference}</h2>
        </div>
        <div className="sidebar--observations">
          <h3>Observaes</h3>
          {props.item.observations ? 
            <p>{props.item.observations}</p> 
            : 
            <p style={{color: "rgb(245 245 245 / 25%)"}}>Nenhuma observao cadastrada.</p>}
        </div>
        <table className="demandsTable">
          <thead>
            <tr>
              <th><h3>Demandas</h3></th>
              <th><h3>Data</h3></th>
              <th><h3>Total: {total}</h3></th>
            </tr>
          </thead>
          <tbody>
            {props.item.demands?.map((data, index) => {
              return (
                <tr className="grid--demand" key={index}>
                  <td><p>{data.store.name}</p></td>
                  <td><p>{data.date}</p> </td>
                  <td><p>{data.quantity}</p></td>
                </tr>
              )
            })}
          </tbody>
        </table>
      </Sidebar>
    </div>
  );
}
