import React, { memo, useEffect, useState } from 'react';

import { Dropdown } from 'primereact/dropdown';
import { TriStateCheckbox } from 'primereact/tristatecheckbox';
import { MultiSelect } from 'primereact/multiselect';

import useProps from '../../hooks/PropsHandlers';
import useCompounds from '../../hooks/CompoundHandlers';
import { useMaterials } from '../../hooks/EstimativeHandlers';

function PhaseFilters(props) {
    const { 
        tags,
        lines, 
        groups, 
        collections,
        stylists,
        designers
    } = useProps()
    const { compounds } = useCompounds()
    const { materials } = useMaterials()

    const [filters, setFilters] = useState({
        reference: "",
        tags: "",
        line: "",
        group: "",
        subgroup: "",
        collection: "",
        material: "",
        stylist: "",
        designer: "",
        compound: "",
        noPhase: false,
        erp: null,
        generic: null
    })

    useEffect(() => {
        props.setFilters(filters)
    }, [filters])

    return ( 
        <>
            <MultiSelect optionLabel="name" value={filters.tags} options={tags} filter
                optionGroupLabel="name" optionGroupChildren="tags" display='chip'
                onChange={(e) => {
                    setFilters(f => ({...f, tags: e.value === undefined ? "" : e.value}))
                }}
                placeholder="Tags"
            />
            <Dropdown 
                value={filters.line}
                options={lines} 
                optionLabel="name" optionValue="id"
                onChange={(e) => {
                    setFilters(f => ({...f, line: e.value === undefined ? "" : e.value}))
                }} 
                showClear={true}
                placeholder="Linha"
            />
            <Dropdown 
                value={filters.group}
                options={groups} 
                optionLabel="name" optionValue="id"
                onChange={(e) => {
                    setFilters(f => ({...f, group: e.value === undefined ? "" : e.value}))
                }} 
                showClear={true}
                placeholder="Grupo"
            />
            {filters.group ?
                <Dropdown 
                    value={filters.subgroup}
                    options={
                        groups?.find(group => group.id === filters.group).subgroups
                    } 
                    optionLabel="name" optionValue="id"
                    onChange={(e) => {
                        setFilters(f => ({...f, subgroup: e.value === undefined ? "" : e.value}))
                    }} 
                    showClear={true}
                    placeholder="Subgrupo"
                /> : null
            }
            <Dropdown 
                value={filters.collection}
                options={collections} 
                optionLabel="name" optionValue="id"
                onChange={(e) => {
                    setFilters(f => ({...f, collection: e.value === undefined ? "" : e.value}))
                }} 
                showClear={true}
                placeholder="Coleo"
            />
            <Dropdown 
                value={filters.compound}
                options={compounds} 
                optionLabel="name" optionValue="id"
                onChange={(e) => {
                    setFilters(f => ({...f, compound: e.value === undefined ? "" : e.value}))
                }} 
                showClear={true}
                placeholder="Composto"
            />
            <Dropdown 
                value={filters.material}
                options={materials} 
                optionLabel="code" optionValue="id"
                onChange={(e) => {
                    setFilters(f => ({...f, material: e.value === undefined ? "" : e.value}))
                }} 
                showClear={true}
                placeholder="Material"
            />
            <Dropdown 
                value={filters.stylist}
                options={stylists} 
                optionLabel="name" optionValue="id"
                onChange={(e) => {
                    setFilters(f => ({...f, stylist: e.value === undefined ? "" : e.value}))
                }} 
                showClear={true}
                placeholder="Estilista"
            />
            <Dropdown 
                value={filters.designer}
                options={designers} 
                optionLabel="name" optionValue="id"
                onChange={(e) => {
                    setFilters(f => ({...f, designer: e.value === undefined ? "" : e.value}))
                }} 
                showClear={true}
                placeholder="Designer"
            />
            <div className='checkfilter'>
                <TriStateCheckbox 
                    inputId="cb2" 
                    onChange={e => setFilters(f => ({...f, erp: e.value === undefined ? "" : e.value}))} 
                    value={filters?.erp} 
                />
                <label htmlFor="cb2" className="p-checkbox-label">Cadastrado ERP</label>
            </div>
            <div className='checkfilter bottom'>
                <TriStateCheckbox 
                    inputId="cb3" 
                    onChange={e => setFilters(f => ({...f, generic: e.value === undefined ? "" : e.value}))} 
                    value={filters?.generic} 
                />
                <label htmlFor="cb3" className="p-checkbox-label">Produto genrico</label>
            </div>
        </>
    )
}

export default memo(PhaseFilters)