import React, { useState, useEffect } from 'react';

import { DataView } from 'primereact/dataview';

import './ListDemands.css'
import ModalBase from '../../../shared/ModalBase/ModalBase';
import ViewDemand from '../ViewDemand/ViewDemand';
import AddDemand from '../AddDemand/AddDemand';
import { useDemands } from '../../../../hooks/DemandsHandlers';

const Demand = (props) => {
  return (
    <div className='p-col demand'>
      <p>{props.data.store.name}</p> <p>{props.data.date}</p> <p>{props.data.quantity}</p> 
    </div>
  )
}

export default function ListDemands(props) {
  const { demands, deleteDemand } = useDemands(props.item.id)

  const itemTemplate = (data, layout) => {
    if (layout === 'list') {
      return (
        <ModalBase
          caller={<Demand data={data} />}
          content={<ViewDemand 
              demand={data} handleDelete={deleteDemand} 
              itemId={props.item.id}
              period={props.period}
            />}
          title={`Demanda para a loja ${data.store.name}`}
        />
      );
    }
  }
  
  return (
    <div className='listDemands'>
      <div className='header'>
        <p>Loja</p>
        <p>Data</p>
        <p>Quantidade</p>
      </div>
      <DataView 
        value={demands}
        layout="list" 
        itemTemplate={itemTemplate}
        emptyMessage="Nenhuma demanda no momento."
      />
      <div className='addContainer'>
        <ModalBase 
          caller={<div className='addButton'>+</div>}
          content={<AddDemand item={props.item} period={props.period}/>}
          title="Adicionar demanda"
        />
      </div>
    </div>
  )
}