import React, { useState, useEffect } from 'react';
import { Calendar } from 'primereact/calendar';
import { ButtonBase } from '@mui/material';
import { InputNumber } from 'primereact/inputnumber';
import { confirmDialog } from 'primereact/confirmdialog';

import './ViewDemand.css'
import { useDemands } from '../../../../hooks/DemandsHandlers';
import ViewLogDemand from '../ViewLogDemand/ViewLogDemand';
import ModalBase from "../../../shared/ModalBase/ModalBase";


export default function ViewDemand(props) {
    const [ details, setDetails ] = useState({
        store: props.demand ? props.demand.store : "",
        quantity: props.demand ? props.demand.quantity : "",
        date: props.demand ? props.demand.date : ""
    })
    const [dateView, setDateView] = useState()
    const [dateRestriction, setDateRestriction] = useState({
        start: null,
        end: null
    })

    const { updateDemand } = useDemands(props.itemId)

    const [editing, setEditing] = useState(false)

    useEffect(() => {
        props.backdropAutoClose(!editing)
    }, [editing])

    useEffect(() => {
        let deltaStart = props.period.start_date.split("-")
        let dateStart = new Date()
        dateStart.setDate(deltaStart[0])
        dateStart.setMonth(deltaStart[1] - 1)
        dateStart.setFullYear(deltaStart[2])

        setDateRestriction((d) => ({...d, start: dateStart}))

        let deltaEnd = props.period.end_date.split("-")
        let dateEnd = new Date()
        dateEnd.setDate(deltaEnd[0])
        dateEnd.setMonth(deltaEnd[1] - 1)
        dateEnd.setFullYear(deltaEnd[2])

        setDateRestriction((d) => ({...d, end: dateEnd}))
    }, [])

    useEffect(() => {
        let delta = details.date.split("-")
        let date = new Date()
        date.setDate(delta[0])
        date.setMonth(delta[1] - 1)
        date.setFullYear(delta[2])

        setDateView(date)
    }, [details.date])


    const handleSaving = () => {
        updateDemand(props.demand.id, details.store.id, details.quantity, details.date)
        setEditing(false)
    }

    const handleCancel = () => {
        if (props.demand) {
            setDetails({...details, quantity: props.demand.quantity})
            setDetails({...details, date: props.demand.date})
        }
        setEditing(false)
    }
    
    const confirmDeletion = () => {
        confirmDialog({
            message: 'Tem certeza que deseja deletar essa demanda?',
            header: 'Deletar demanda',
            icon: 'pi pi-exclamation-triangle',
            acceptLabel: 'Sim',
            rejectLabel: 'No',
            accept: () => props.handleDelete(props.demand.id),
            reject: () => {}
        });
    }

    return (
        <div className='viewDemand'>
            <div className="p-grid p-dir-col">
                <div className="p-col">
                    <p>Quantidade</p>
                    <InputNumber value={details.quantity} 
                        locale="pt-BR"
                        showButtons
                        min={0}
                        minFractionDigits={0} maxFracionDigits={0}
                        onValueChange={(e) => {
                        setDetails({ 
                            ...details, quantity: e.value })
                        }}
                        disabled={!editing}
                    />
                </div>
                <div className="p-col">
                    <p>Data</p>
                    <Calendar 
                        value={dateView}
                        dateFormat="dd/mm/yy"
                        minDate={dateRestriction.start}
                        maxDate={dateRestriction.end}
                        disabled={!editing}
                        onChange={(e) => {
                            setDetails(d => ({...d, 
                                date: `${e.value.getDate()}-${e.value.getMonth() + 1}-${e.value.getFullYear()}`
                            }))
                        }}
                    />
                </div>
                <div className="p-col demandCaller log">
                    <ModalBase
                        title="Histrico da demanda"
                        caller={
                            <div>
                                <p>Histrico da demanda</p>
                            </div>
                        }
                        content={<ViewLogDemand demandId={props.demand.id}/>}
                    />
                </div>
                <div className='buttons'>
                    { !editing ?
                        <ButtonBase onClick={(e) => setEditing(!editing)} className='greenButton'>
                            Editar
                        </ButtonBase> 
                        : 
                        <ButtonBase onClick={handleCancel} className='redButton'>
                            Cancelar
                        </ButtonBase>
                    }
                    { editing ?
                        <ButtonBase onClick={(e) => handleSaving()} className='greenButton'>
                        Salvar
                        </ButtonBase> 
                        : 
                        <ButtonBase onClick={confirmDeletion} className='redButton'>
                        Deletar
                        </ButtonBase>
                    }
                </div>
            </div>
        </div>
    )
}