import { useCallback } from 'react'
import useSWR from 'swr'
import api from '../services/api'

const fetchDemands = async (url) => api.get(url).then(res => res.data)

export function useDemands (itemId) {
    const { data, error, mutate } = useSWR(
        `/compounds/items/demands?item=${itemId}`, 
        fetchDemands,
        {
            revalidateIfStale: false,
            revalidateOnFocus: false,
        }
    )

    const addDemand = useCallback((itemId, storeId, quantity, date) => {
        api.post("/compounds/items/demands", {
            item: itemId,
            store: storeId,
            quantity: quantity,
            date: date
        }).then(() => {
            mutate()
        }).catch((err) => console.log(err.response))
    }, [])

    const updateDemand = useCallback((demandId, storeId, quantity, date) => {
        api.patch(`/compounds/items/demands/${demandId}`, {
            store: storeId,
            quantity: quantity,
            date: date
        }).then(() => {
            mutate()
        }).catch((err) => console.log(err.response))
    }, [])

    const deleteDemand = useCallback(id => {
        api.delete(`compounds/items/demands/${id}`).then(() => {
            mutate()
        }).catch((err) => {
            console.log(err.response)
            alert("Operao no concluda.")
        })
    }, [])

    const logDemand = useCallback(async id => {
        return api.get(`compounds/items/demands/${id}/history`)
        .then(res => res.data)
        .catch(
            err => console.log(err.response)
        )
    }, [])

    return {
        demands: data,
        isLoading: !error && !data,
        isError: error,
        addDemand,
        deleteDemand,
        updateDemand,
        logDemand
    }
}

export default function useStores() {
    const { data, error } = useSWR(
        "/compounds/stores", 
        fetchDemands,
        {
            revalidateIfStale: false,
            revalidateOnFocus: false,
        }
    )

    return {
        stores: data,
        isLoading: !error && !data,
        isError: error
    }
}

