import React, { useEffect, useState } from 'react'
import './style.css'
import 'primeflex/primeflex.css';

import { TextField } from '@mui/material'

import SideBar from '../../components/SideBar/SideBar'
import PhaseCard from '../../components/PhaseCard/PhaseCard';

import { isAuthenticated } from '../../services/api';
import usePhases from '../../hooks/PhaseHandlers';

const PhasesPage = () => {
    useEffect(() => {
        isAuthenticated()
    }, [])

    const { 
        phases, 
        isLoading
    } = usePhases()
    
    const [filters, setFilters] = useState({})

    return (
        <div className='phasePage page'>
            <SideBar 
                setFilters={setFilters}
            />
            <div className='phasePage--body'>
                <div className='header'>
                    <div className='header--content'>
                        <h2>Estes so os Mix de PROCESSOS</h2>
                        <div className='searchBar'>
                            <TextField id="outlined-basic" 
                                value={filters.reference ? filters.reference : ""}
                                label="Pesquisar" 
                                variant="outlined" 
                                size="small"
                                color="success"
                                onChange={(e) => setFilters(f => ({...f, reference: e.target.value}))}
                            />
                        </div>
                    </div>
                    <div className='header--border'></div>
                </div>
                <div className='main'>
                    { isLoading ? 
                    <h3 id="loadingText">Carregando...</h3> 
                    :
                        (
                            phases?.map((phase, index) => (
                                <div  key={index} className='phaseContainer'>
                                    <PhaseCard 
                                        title={phase.phase}
                                        data={phase.products}
                                        phase={phase}
                                        allPhases={phases}
                                        filters={filters}
                                    />
                                </div>)
                            )
                        )
                    }
                </div>
            </div>
        </div>
    )
}

export default PhasesPage